import globalStyle from "../../components/app/app.module.css";
import {Button,  Input, PasswordInput} from "@ya.praktikum/react-developer-burger-ui-components";
import {useNavigate} from "react-router-dom";
import {INPUT, PATH, SIZE} from "../../utils/constant";
import {postResetPasswordThunk} from "../../services/actions/recover-password";
import {useDispatch, useSelector} from "../../services/hook";
import React, {FormEvent, useRef, useState} from "react";


export const ResetPasswordPage = () => {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const {forgotPasswordSuccess, resetPasswordSuccess, isAuth} = useSelector((state) => state.access)

    const [ formValue, setFormValue ] = useState({
        password: '',
        token: '',
    })

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setFormValue({
            ...formValue,
            [e.target.name]: e.target.value,
        })
    }

    const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
        e.preventDefault()
        dispatch(postResetPasswordThunk(formValue))
    }

  const inputRef = useRef<HTMLInputElement>(null)
  const onClickLogin = () => {
    navigate(PATH.LOGIN)
  }
  const handleIconClick = (): void => {
     if (inputRef && inputRef.current) {
         inputRef.current.focus()
     }
  }

  if(!forgotPasswordSuccess) {
    navigate(PATH.FORGOT_PASSWORD)
  } else if (resetPasswordSuccess) {
    navigate(PATH.LOGIN)
  } else if (isAuth) {
    navigate(PATH.HOME)
  }

  return(
    <form className={`${globalStyle.columnGrid} + mt-20`} onSubmit={handleSubmit}>
      <p className="text text_type_main-medium"> </p>
      <PasswordInput
        onChange={handleChange}
        value={(formValue as {password: string}).password || ''}
        placeholder={'  '}
        name={'password'}
      />
      <Input
        type={'text'}
        placeholder={'   '}
        onChange={handleChange}
        value={(formValue as {token: string}).token || ''}
        name={INPUT.NAME.CODE}
        error={false}
        ref={inputRef}
        onIconClick={handleIconClick}
        errorText={''}
        size={'default'}
        extraClass="ml-1"
      />
      <Button htmlType="submit" type="primary" size="medium" extraClass="mb-10">
        
      </Button>
      <div className={`${globalStyle.formTipContainer}`}>
        <span className="text text_type_main-default text_color_inactive"> ?
          <Button htmlType="button" type="secondary" size={SIZE.MEDIUM} onClick={onClickLogin}>
            
          </Button>
        </span>
      </div>
    </form>
  )
}
